.MainPage_header__background__1MoyP::before {
  content: "";
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
  -webkit-backdrop-filter: blur(8px); /* apply the blur */
  backdrop-filter: blur(8px); /* apply the blur */
  pointer-events: none; /* make the pseudo class click-through */
}

.MainPage_header__background__1MoyP {
  z-index: -1;
  grid-row: 1 / 2;
  grid-column: 1 / 4;
  /* filter: blur(8px);
  -webkit-filter: blur(8px); */
  z-index: -3;
  /* transform: scale(1.1); */
  background: linear-gradient(
      rgba(0, 0, 0, 0.8),
      rgba(0, 0, 0, 0.2),
      rgba(0, 0, 0, 0)
    ),
    linear-gradient(
      90deg,
      rgba(0, 0, 0, 0.8),
      rgba(0, 0, 0, 0.2),
      rgba(0, 0, 0, 0)
    ),
    linear-gradient(
      270deg,
      rgba(0, 0, 0, 0.8),
      rgba(0, 0, 0, 0.2),
      rgba(0, 0, 0, 0)
    ),
    url(/static/media/bck.63eccec8.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
