.mainContainer {
  width: 100%;
  height: 2400px;
  flex-wrap: wrap;
}
.mainContainer__grid {
  display: grid;
  grid-template-rows: 700px [row1] 700px [row2] 700px [row3] 300px [row4];
}

@media (min-width: 1921px) {
  .mainContainer__grid {
    grid-template-columns:
      minmax(80px, 1fr) [row1] 1440px [row2] minmax(80px, 1fr)
      [row3];
    justify-content: center;
  }
  h1 {
    font-size: 88px;
  }
  h2 {
    font-size: 28px;
  }
  h3 {
    font-size: 44px;
  }
  h4 {
    font-size: 20px;
  }
  h5 {
    font-size: 18px;
  }
}

@media (max-width: 1920px) {
  .mainContainer__grid {
    grid-template-columns:
      minmax(80px, 1fr) [col1] 864px [col2] minmax(80px, 1fr)
      [col3];
  }
  h1 {
    font-size: 88px;
  }
  h2 {
    font-size: 28px;
  }
  h3 {
    font-size: 44px;
  }
  h4 {
    font-size: 20px;
  }
  h5 {
    font-size: 18px;
  }
}
